import React from 'react'
import styled from 'styled-components'





function Pin(props) {
    return (
        <Wrapper>
           <Container>
                <img src={props.Img} />
           </Container>
        </Wrapper>
    )
}

export default Pin

const Wrapper=styled.div`
    display:inline-flex;
    padding:8px;
    
`

const Container=styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    box-sizing:border-box;
    cursor:pointer;
    width:236px;
    img{
        width:100%;
        display:flex;
        cursor:zoom-in;
        border-radius:10px;
        object-fit:cover;

    }
    img:hover{
        transform:scale(1.4);
        z-index:10;
        border-radius:5px;
        box-shadow: 6px 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.29);
    }
`