import React from 'react'
import styled from 'styled-components'
import AllInclusiveIcon from '@material-ui/icons/AllInclusive';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';

function Header() {
    return (
        <Wrapper>
            <LogoWrapper>
                <a href="index.js">
                    <img src="img/logolight.png"/>
                </a>
            </LogoWrapper>
            <HomePageButton>
                <Tooltip title="Homepage">
                    <IconButton color="secondary" aria-label="Homepage">
                        <AllInclusiveIcon fontSize="large" />
                    </IconButton>
                </Tooltip>
                <Tooltip title="Linkedin">
                    <IconButton color="secondary" onClick={() => window.open('https://www.linkedin.com/in/anushkabajpai1006/', "_blank")}  aria-label="Homepage">
                        <LinkedInIcon fontSize="large" />
                    </IconButton>
                </Tooltip>
                <Tooltip title="GitHub">
                    <IconButton color="secondary" onClick={() => window.open('https://github.com/Anushka1001', "_blank")} aria-label="Homepage">
                        <GitHubIcon fontSize="large"/>
                    </IconButton>
                </Tooltip>
            </HomePageButton>

        </Wrapper>
    )
}

export default Header

const Wrapper=styled.div`
    display:flex;
    align-items:center;
    height:65px;
    margin:15px 60px;
    position:static;
    background:none;
    top:0;
    left:0;
    right:0;
    align-items:center;
    justified-content:space-between;
`

const LogoWrapper=styled.div`
    margin:10px 10px 5px 10px;
    img{
        height:70px;
        width:auto;
        padding-top:20px;
        transition:transform 0.2s;
    }
    img:hover{
        transform:scale(1.2);
    }
`

const HomePageButton=styled.div`
    float:right;
    margin-left:auto;
    
`

